html {
  overflow: hidden;
}

body {
  margin: 0;
  background-color: #2e3339;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  font-family: 'Open Sans', sans-serif;
}
